::-webkit-scrollbar {
  width: 6px;
  height: 4px;
    background: rgba(0, 0, 0, 0.5);
}

::-webkit-scrollbar-thumb {
  cursor: pointer;
  
  background-color: #27282F;
    border-radius: 30px;
}
/*-----------------------LIVE CHAT-------------------------*/

.cgl-live-chat{
    height: calc(100% - 60px);
}

.cgl-live-chat .chat-wrapper {
    /* background-color: rgba(0, 0, 0, 0.5); */
    box-sizing: border-box;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    height: -webkit-fill-available;
}

.cgl-live-chat .chat-wrapper .chat-title {
    padding: 10px;
    font-family: Hind Madurai;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
    color: #f8f8f8;
}

.cgl-live-chat .chat-wrapper .chat-view {
    background-color: #FFFFFF;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 10px;
    flex: 1 1 auto;
    word-break: break-word;
}

.cgl-live-chat .chat-wrapper .chat-item {
    padding: 5px;
}

.cgl-live-chat .chat-wrapper .chat-view img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.cgl-live-chat .chat-wrapper .chat-view .sender-name a {
    font-family: Hind Madurai;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 12px;
    text-transform: uppercase;

    color: #070707;
}

.cgl-live-chat .chat-wrapper .chat-view .sender-name a:hover {
    color: #000000;
    font-weight: bold;
}

.cgl-live-chat .chat-wrapper .chat-view .admin-tag a {
    background-color: #4368EA !important;
    color: #000000 !important;
    border-radius: 9px;
    padding: 0 8px;
}

.cgl-live-chat .chat-wrapper .chat-view .chat-time {
    font-family: Hind Madurai;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 11px;
    padding-left: 8px;
    padding-right: 8px;
    color: #585C61;
}

.cgl-live-chat .chat-wrapper .chat-view .chat-time i {
    display: none;
}

.cgl-live-chat .chat-wrapper .chat-view .chat-pinned {
    border: 2px solid #7389DC;
    box-sizing: border-box;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: #11151A;
}

.chat-sticky {
    position: fixed;
    top: 0;
    width: 100%;
}

.cgl-live-chat .chat-wrapper .chat-view .chat-pinned .chat-time i {
    display: inline;
}

.cgl-live-chat .chat-wrapper .chat-view .chat-text {
    font-family: Hind Madurai;
    font-style: normal;
    font-weight: lighter;
    font-size: 14px;
    line-height: 17px;
    padding: 3px;
    color: #9FADC5;
}

.cgl-live-chat .chat-wrapper .chat-message {
    background-color: rgba(0, 0, 0, 0.5);
    color: #FFFFFF;
    padding: 6px;
}

.cgl-live-chat .chat-wrapper .chat-message input {
    background-color: transparent;
    color: #c0c0c0;
    border: none !important;
    font-family: Hind Madurai;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 22px;
    outline: none !important;
}

.cgl-live-chat .chat-wrapper .chat-message button {
    background-color: transparent;
    border: none;
    outline: none;
}
.cgl-live-chat .chat-wrapper .dropdown-menu{
    background-color: #141921;
}
.cgl-live-chat .chat-wrapper .dropdown-menu .dropdown-item{
    color: #9FADC5;
    font-family: Hind Madurai;
    font-style: normal;
    font-weight: 300;
}
.cgl-live-chat .chat-wrapper .dropdown-menu .dropdown-item:hover{
    background-color: #1C232E;
}
@media(max-width:450px) {
    .cgl-live-chat .chat-wrapper .chat-view {
        max-height: 300px;
    }

    .cgl-live-chat .chat-wrapper .chat-view img {
        width: 30px;
        height: 30px;
    }

    .cgl-live-chat .chat-wrapper .chat-view .sender-name a {
        font-size: 10px;
        line-height: 10px;
    }

    .cgl-live-chat .chat-wrapper .chat-view .chat-time {
        font-weight: 500;
        font-size: 10px;
        line-height: 10px;
        padding-left: 8px;
        padding-right: 8px;
    }

    .cgl-live-chat .chat-wrapper .chat-view .chat-text {
        font-size: 12px;
        line-height: 12px;
        padding: 3px;
    }
}

.dropdown-btn{
 background-color: transparent;
    color: #585C61;
    border: none;
    outline: none !important;
}